import React, { useState } from 'react'
import { Card, Row, Col, Button, Table, Popconfirm, Icon } from 'antd';
import FormSearch from './FormSearch';
import { getPaymentData, createPaymentRequest } from '../../../services/payment';
import CardTitle from '../../../components/common/CardTitle/index';
import { moneyToString } from '../../../utils/formatUtil';

export default (props = {}) => {
  const [yearEduId, setYearEduId] = useState(props.yearEduId);
  const [semesterId, setSemesterId] = useState(props.semesterId);
  const [paymentItems, setPaymentItems] = useState([]);
  const [paymentSelectedRowKeys, setPaymentSelectedRowKeys] = useState([]);
  const [selectedPaymentItems, setSelectedPaymentItems] = useState([]);
  const [summaryPaymentItems, setSummaryPaymentItems] = useState({});
  const [isFetching, setIsFetching] = useState(false)

  const handleClearTable = () => {
    setPaymentItems([])
    setPaymentSelectedRowKeys([])
  }

  const redirectToPayU = async ({ url, method = 'POST', data } = {}) => {
    const payload = {
      ...data
    };
    const form = document.createElement('form');
    form.style.visibility = 'hidden';
    form.method = method;
    form.action = url;

    const keys = Object.keys(payload);
    keys.forEach((key) => {
      const input = document.createElement('input');
      input.name = key;
      input.value = payload[key];
      form.appendChild(input);
    })

    document.body.appendChild(form);
    form.submit();
  }

  const callGetPaymentData = async (query) => {
    setIsFetching(true)
    const [result, err] = await getPaymentData({ ...query })
    setIsFetching(false)

    if (err) {
      handleClearTable();
      return;
    }

    setPaymentSelectedRowKeys([])
    setPaymentItems([...result.data])
  }

  const callCreatePaymentRequest = async () => {
    setIsFetching(true)
    const payload = {
      yearEduId,
      semesterId,
      paymentItems: selectedPaymentItems
    }

    const [result, err] = await createPaymentRequest(payload)
    if (err) {
      return;
    }

    const { data, encryptedKey, partnerIdentity, redirectUrl } = result.data
    redirectToPayU({ url: redirectUrl, data: { data, encryptedKey, partnerIdentity } });
  }

  const onSearch = async (values) => {
    setYearEduId(values.yearEduId);
    setSemesterId(values.semesterId);
    await callGetPaymentData(values);
  }

  const handleCreatePayment = async () => {
    await callCreatePaymentRequest()
  }

  const calcSelectedPaymentItems = (selectedRowKeys = []) => {
    const selectedItems = paymentItems.filter((x, index) => selectedRowKeys.includes(index))
    let totalAmount = 0
    let reduceAmount = 0
    let paymentAmount = 0
    selectedItems.forEach(item => {
      totalAmount += item.totalAmount
      reduceAmount += item.reduceAmount
      paymentAmount += item.paymentAmount
    })

    setSelectedPaymentItems(selectedItems)
    setSummaryPaymentItems({ totalAmount, reduceAmount, paymentAmount })
  }

  const onSelectChange = (selectedRowKeys) => {
    setPaymentSelectedRowKeys(selectedRowKeys)
    calcSelectedPaymentItems(selectedRowKeys)
  };

  const columns = [
    {
      title: 'Học phí',
      dataIndex: 'label',
    },
    {
      title: 'Số tín chỉ',
      dataIndex: 'noOfCredit',
      width: '150px',
      align: 'right'
    },
    {
      title: 'Số tiền',
      dataIndex: 'unitPrice',
      width: '150px',
      align: 'right',
      render: (text) => text && moneyToString(text)
    },
    {
      title: 'Tổng tiền',
      dataIndex: 'totalAmount',
      width: '150px',
      align: 'right',
      render: (text) => text && moneyToString(text)
    },
    {
      title: 'Giảm',
      dataIndex: 'reduceAmount',
      width: '150px',
      align: 'right',
      render: (text) => text && moneyToString(text)
    },
    {
      title: 'Thành tiền',
      dataIndex: 'paymentAmount',
      width: '150px',
      align: 'right',
      render: (text) => text && moneyToString(text)
    },
  ]

  const rowSelection = {
    paymentSelectedRowKeys,
    onChange: onSelectChange,
  };

  return (
    <React.Fragment>
      <Card className="payment-sumarry-items">
        <CardTitle title="Thanh toán học phí" />
        <Row>
          <Col span={24}>
            <FormSearch
              onSubmit={onSearch}
              yearEduId={yearEduId}
              semesterId={semesterId}
            />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              rowSelection={rowSelection}
              columns={columns}
              dataSource={paymentItems}
              pagination={false}
              loading={isFetching}
            />
          </Col>
        </Row>
        <br />
        <Row>
          <Col offset={20} span={4}>
            <Row>
              <Col span={18}>Tổng tiền</Col>
              <Col span={6} style={{ textAlign: 'right' }}>{moneyToString(summaryPaymentItems.totalAmount || 0)}</Col>
            </Row>
            <Row>
              <Col span={18}>Giảm</Col>
              <Col span={6} style={{ textAlign: 'right' }}>{moneyToString(summaryPaymentItems.reduceAmount || 0)}</Col>
            </Row>
            <Row>
              <Col span={18}>Thành tiền</Col>
              <Col span={6} style={{ textAlign: 'right' }}>{moneyToString(summaryPaymentItems.paymentAmount || 0)}</Col>
            </Row>
            <br />
            <Row>
              <Col span={12} offset={12} style={{ textAlign: 'right' }}>
                <Popconfirm
                  title="Bạn có chắc muốn thanh toán？"
                  icon={<Icon type="question-circle-o" style={{ color: 'red' }} />}
                  disabled={!selectedPaymentItems || selectedPaymentItems.length === 0}
                  onConfirm={handleCreatePayment}
                >
                  <Button
                    type="primary"
                    disabled={!selectedPaymentItems || selectedPaymentItems.length === 0}
                    loading={isFetching}
                  >
                    Thanh toán
                  </Button>
                </Popconfirm>
              </Col>
            </Row>
          </Col>
        </Row>
      </Card >
    </React.Fragment>
  )
}


