import React, { useState } from 'react';
import { Button, Spin } from 'antd';
import './style.scss';

const Btn = props => {
  const [loading, setLoading] = useState(false);

  const handleClick = async (e) => {
    if (loading) {
      return;
    }
    if (!props.onClick) {
      return;
    }
    setLoading(true);
    await props.onClick(e);
    setLoading(false);
  };

  const getClassName = () => {
    let className = '';
    if (props.primary) {
      className = 'btn btn-primary';
    } else  {
      className = 'btn btn-default';
    }
    
    if (loading) {
      className += ' btn-loading ';
    }
    className += props.className;
    return className;
  };

  return (
    <Button disabled={loading} {...props} onClick={handleClick} className={getClassName()}>
      <span>{props.children || props.title}</span>
      {loading && <Spin size="small" />}
    </Button>
  );
};

Btn.defaultProps = {
  className: '',
  title: '',
};

export default Btn;



